var consturl,imgg=[];
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
export default {
	data() {
		return {
			type:"that.type",
			jobtype:"repair",
			remark :"",
			address :null,
			reservedTime:0,
			openid: "",
			jobNo:"",
			indexColor:0,
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]

		}
	},
	onLoad(options) {
		
		consturl=this.$consturl;
		this.openid=uni.getStorageSync("openid");
		this.imageList = [],
		this.sourceTypeIndex = 2,
		this.sourceType = ['拍照', '相册', '拍照或相册'],
		this.sizeTypeIndex = 2,
		this.sizeType = ['压缩', '原图', '压缩或原图'],
		this.countIndex = 8;
	},
	onShow:function(){
		this.address=uni.getStorageSync("adress");
	},
	methods: {	
		clickAdress:function(){
			uni.navigateTo({
				url: '../address/pickOtherAddress'
			});
		},
		chooseImage: async function() {









		
			if (this.imageList.length === 9) {
				
				console.log("已经九张了");
					return;
			}
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
					// console.log(res.tempFilePaths);
					var tempFilePaths =res.tempFilePaths ;
					uni.uploadFile({
						url:"https://www.ccweb.xyz/gas/file/upload",
						filePath:tempFilePaths[0],
						name:"file",
						success:function(res){
							let a=JSON.parse(res.data).data;
							imgg.push(a);
							
							console.log(imgg);
							
							
							console.log("图片");
						}
					})
					
				},
				fail: (err) => {






					uni.getSetting({
						success: (res) => {
							let authStatus = false;
							switch (this.sourceTypeIndex) {
								case 0:
									authStatus = res.authSetting['scope.camera'];
									break;
								case 1:
									authStatus = res.authSetting['scope.album'];
									break;
								case 2:
									authStatus = res.authSetting['scope.album'] && res.authSetting['scope.camera'];
									break;
								default:
									break;
							}
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要从您的相机或相册获取图片，请在设置界面打开相关权限',
									success: (res) => {
										if (res.confirm) {
											uni.openSetting()
										}
									}
								})
							}
						}
					})

				}
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		imgDel:function(imgIndex){
			this.imageList.splice(imgIndex,1);
		},
		async checkPermission(code) {
			let type = code ? code - 1 : this.sourceTypeIndex;
			let status = permision.isIOS ? await permision.requestIOS(sourceType[type][0]) :
				await permision.requestAndroid(type === 0 ? 'android.permission.CAMERA' :
					'android.permission.READ_EXTERNAL_STORAGE');
		
			if (status === null || status === 1) {
				status = 1;
			} else {
				uni.showModal({
					content: "没有开启权限",
					confirmText: "设置",
					success: function(res) {
						if (res.confirm) {
							permision.gotoAppSetting();
						}
					}
				})
			}
		
			return status;
		},
		// 请输入问题详情
		textarea:function(e){
			this.remark=e.detail.value;
		},
		// 问题类型点击事件
		callbacktext:function(text){
			let that=this;
			if(text==1){
				that.type="室温不达标";
				that.indexColor=1;
			};
			if(text==2){
				that.type="散热器温度不足";
				that.indexColor=2;
			};
			if(text==3){
				that.type="管道漏水";
				that.indexColor=3;
			};
			if(text==4){
				that.type="暖气异响";
				that.indexColor=4;
			};
			if(text==5){
				that.type="暖气管道阻塞";
				that.indexColor=5;
			};
			if(text==6){
				that.type="其他";
				that.indexColor=6;
			};
			if(text==7){
				that.type="其他";
				that.indexColor=7;
			};
		},
		// 用户报修单提交
		click: function(e) {
			let that=this;
			console.log(consturl + "/wx/api/Repair");
			// console.log(e);
			uni.request({
				url: consturl + "wx/api/Repair", //
				data: {
					type:that.type,
					remark :that.remark,
					address:JSON.stringify(that.address),
					reservedTime:0,
					openid: that.openid,
					imgurl:imgg,
					jobtype:119
				},
				header: {
						'Content-Type':'application/x-www-form-urlencoded;charset=utf-8'
				      },
				method: 'POST',
				success: function (res) {
						console.log(JSON.stringify(that.address));
						that.jobNo=res.data.data;
						console.log(that.jobNo);
						uni.navigateBack({
							delta:1
						});
				},
				fail:function(){
					console.log("complete");
				}
			});
			
			
			
			
		}
		
	}
}
